import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

import { Chart } from 'chart.js';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-income-tracker-ageing',
  templateUrl: './income-tracker-ageing.component.html',
  styleUrls: ['./income-tracker-ageing.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class IncomeTrackerAgeingComponent implements OnInit {
  
  isDataLoaded:boolean = false;

  isChartLoaded:boolean = false;

  ageingData:string = "";
  ageingDataDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  totalInvoices:any = 0;

  dataType:string = 'DueAge';
  asOfDate:any;

  totalAmountItem1:any;
  totalAmountItem3:any;
  totalAmountItem4:any;
  totalAmountItem5:any;
  totalAmount:any;

  totalAmountItem1Percent:any;
  totalAmountItem3Percent:any;
  totalAmountItem4Percent:any;
  totalAmountItem5Percent:any;

  ageingUnitDataList:any;

  chart: any;
  @ViewChild('incomePieChart', {static: false}) private chartRef;

  timeZone:any;

  constructor(
    private accountsService:AccountsService,
    private sharedService: SharedService,
  	private sessionService:SessionService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getDate(date){
    return moment(date).add(this.timeZone.offset, 'hours').format(this.timeZone.date);
  }

  getDateTime(date){
    return moment(date).add(this.timeZone.offset, 'hours').format(this.timeZone.time);
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  setDataType(type){
    this.dataType = type;
    this.getAgeingData();
  }

  drawChart() {

    setTimeout(()=> {

      this.isChartLoaded = true;

      var data = {
      labels: [
        "0-30 Days",
        "31-60 Days",
        "61-90 Days",
        "Over 90 Days"
      ],
      datasets: [
        {
          data: [parseFloat(this.totalAmountItem1Percent), parseFloat(this.totalAmountItem3Percent), parseFloat(this.totalAmountItem4Percent), parseFloat(this.totalAmountItem5Percent)],
          backgroundColor: [
            "#06c987",
            "#db5c9e",
            "#0263bd",
            "#ff7373"
          ],
          hoverBackgroundColor: [
            "#06c987",
            "#db5c9e",
            "#0263bd",
            "#ff7373"
          ],
          borderWidth:0
     }]
    };



    this.chart = new Chart(this.chartRef.nativeElement, {
      type: 'doughnut',
      data: data,
      options: {
        responsive: true,
        legend: {
          display: false
        },
        cutoutPercentage: 60,
            rotation: 120,
        animation: {
                onComplete: (e) => {
             
                }
            }
    }
    });

    }, 500);

  }

  getAgeingData(){


  this.totalAmountItem1 = 0;
  this.totalAmountItem3 = 0;
  this.totalAmountItem4 = 0;
  this.totalAmountItem5 = 0;
  this.totalAmount = 0;

  this.totalAmountItem1Percent = 0;
  this.totalAmountItem3Percent = 0;
  this.totalAmountItem4Percent = 0;
  this.totalAmountItem5Percent = 0;

  this.totalInvoices = 0;

    this.asOfDate = _moment().toISOString(); // todays date

    var ageingParams = {
      ApartmentID: this.sessionService.apartmentId,
      asOfDate: this.asOfDate,
      type: this.dataType,
      AgeFrom1: 0,
      AgeTo1: 10,
      AgeFrom2: 11,
      AgeTo2: 30,
      AgeFrom3: 31,
      AgeTo3: 60,
      AgeFrom4: 61,
      AgeTo4: 90,
      AgeFrom5: 91,
      AgeTo5: null
    }

    this.isDataLoaded = false;
    this.isChartLoaded = false;

    this.accountsService.getArAgingCollectionDueSummary(ageingParams).subscribe((res:any) => {

      this.isDataLoaded = true;

      this.ageingDataDataList = res;

      _.each(this.ageingDataDataList, item => {
        item.towerUnit = item.apartmentBlockNumber + ' ' + item.apartmentBlockUnitNumber;
      })


      this.totalItems = this.ageingDataDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      _.each(this.ageingDataDataList, (item, index) => {

        this.totalInvoices = this.totalInvoices + item.bucketCount1 + item.bucketCount2 + item.bucketCount3 + item.bucketCount4 + item.bucketCount5;

        this.totalAmountItem1 = this.totalAmountItem1 + item.bucketAmt1 + item.bucketAmt2;
        this.totalAmountItem3 = this.totalAmountItem3 + item.bucketAmt3;
        this.totalAmountItem4 = this.totalAmountItem4 + item.bucketAmt4;
        this.totalAmountItem5 = this.totalAmountItem5 + item.bucketAmt5;

        this.totalAmount = this.totalAmountItem1 + this.totalAmountItem3 + this.totalAmountItem4 + this.totalAmountItem5;

        this.totalAmountItem1Percent = (this.totalAmountItem1/this.totalAmount)*100;
        this.totalAmountItem3Percent = (this.totalAmountItem3/this.totalAmount)*100;
        this.totalAmountItem4Percent = (this.totalAmountItem4/this.totalAmount)*100;
        this.totalAmountItem5Percent = (this.totalAmountItem5/this.totalAmount)*100;


        if(index == this.ageingDataDataList.length-1){
          this.drawChart();
        }

        var unitParams = {
          ApartmentID: this.sessionService.apartmentId,
          asOfDate: this.asOfDate,
          unitId: item.unitID
        }

        this.accountsService.getArAgingCollectionDueByUnitId(unitParams).subscribe((res:any) => {
          item.invoiceDetails = res;
        },
        error => {
          
        }
        );

      });

    });

  }


  ngOnInit() {
    
    this.getAgeingData();
    
    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

  }

  ngAfterViewInit(){

    
    
    

  }

}
